
'use client';

import * as React from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import { FilePenLine, Trash2, Mic } from "lucide-react";
import type { SavedSpeech, User, WrittenSpeech, PracticeMode } from "@/lib/types";
import { useAuth } from "@/contexts/auth-context";
import { useToast } from "@/hooks/use-toast";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";
import { format } from "date-fns";
import { WriteSpeechDialog } from "@/components/dashboard/write-speech-dialog";

const WRITTEN_SPEECHES_STORAGE_KEY = 'work-session-written-speeches';
const SAVED_SPEECHES_STORAGE_KEY = 'work-session-saved-speeches';

type PerformanceEventType = 'humorous' | 'dramatic' | 'duo' | 'duet' | 'prose' | 'poetry';

const eventTypeMap: Record<PerformanceEventType, string> = {
  humorous: 'Humorous Interpretation (HI)',
  dramatic: 'Dramatic Interpretation (DI)',
  duo: 'Duo Interpretation (DUO)',
  duet: 'Duet Acting (DA)',
  prose: 'Prose (PR)',
  poetry: 'Poetry (PO)',
};

const SavedScripts = ({
  eventType,
  writtenSpeeches,
  setSpeechToEdit,
  setIsWritingDialogOpen,
  handleDeleteWrittenSpeech,
  user,
}: {
  eventType: PerformanceEventType;
  writtenSpeeches: WrittenSpeech[];
  setSpeechToEdit: (speech: WrittenSpeech | null) => void;
  setIsWritingDialogOpen: (open: boolean) => void;
  handleDeleteWrittenSpeech: (id: string) => void;
  user: User | null;
}) => {
  const relevantSpeeches = React.useMemo(() => {
    if (!user) return [];
    return writtenSpeeches
      .filter((s) => s.mode === eventType && s.ownerId === user.id)
      .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
  }, [writtenSpeeches, user, eventType]);

  return (
    <Card>
      <CardHeader>
        <div className="flex items-start justify-between gap-4">
          <div>
            <CardTitle>Saved Scripts</CardTitle>
            <CardDescription>
              View, edit, and manage your written pieces for this event.
            </CardDescription>
          </div>
          <Button
            variant="outline"
            size="sm"
            onClick={() => {
              setSpeechToEdit(null);
              setIsWritingDialogOpen(true);
            }}
          >
            <FilePenLine className="mr-2 h-4 w-4" />
            New Script
          </Button>
        </div>
      </CardHeader>
      <CardContent>
        {relevantSpeeches.length > 0 ? (
          <ul className="space-y-2">
            {relevantSpeeches.map((speech) => (
              <li
                key={speech.id}
                className="flex items-center justify-between p-3 rounded-md border bg-muted/50"
              >
                <div
                  className="flex-1 min-w-0 cursor-pointer"
                  onClick={() => {
                    setSpeechToEdit(speech);
                    setIsWritingDialogOpen(true);
                  }}
                >
                  <p className="text-sm font-medium truncate">{speech.title}</p>
                  <p className="text-xs text-muted-foreground line-clamp-1">
                    {speech.body}
                  </p>
                </div>
                <div className="flex items-center gap-1 flex-shrink-0 ml-4">
                    <Button variant="default" size="sm" disabled>
                        <Mic className="mr-2 h-4 w-4" /> Practice
                    </Button>
                    <AlertDialog>
                        <AlertDialogTrigger asChild>
                        <Button variant="destructive" size="icon" className="h-9 w-9">
                            <Trash2 className="h-4 w-4" />
                        </Button>
                        </AlertDialogTrigger>
                        <AlertDialogContent>
                        <AlertDialogHeader>
                            <AlertDialogTitle>Delete Script?</AlertDialogTitle>
                            <AlertDialogDescription>
                            This will permanently delete "{speech.title}". This action
                            cannot be undone.
                            </AlertDialogDescription>
                        </AlertDialogHeader>
                        <AlertDialogFooter>
                            <AlertDialogCancel>Cancel</AlertDialogCancel>
                            <AlertDialogAction
                            onClick={() => handleDeleteWrittenSpeech(speech.id)}
                            >
                            Delete
                            </AlertDialogAction>
                        </AlertDialogFooter>
                        </AlertDialogContent>
                    </AlertDialog>
                </div>
              </li>
            ))}
          </ul>
        ) : (
          <div className="text-center text-sm text-muted-foreground py-4">
            No scripts saved for this event.
          </div>
        )}
      </CardContent>
    </Card>
  );
};


const SavedRecordings = ({ eventType, savedSpeeches, user, allUsers }: { 
    eventType: PerformanceEventType, 
    savedSpeeches: SavedSpeech[],
    user: User | null,
    allUsers: User[],
}) => {

     const relevantRecordings = React.useMemo(() => {
        if (!user) return [];
        return savedSpeeches.filter(s => s.mode === eventType && (s.ownerId === user.id || (s.sharedWith || []).includes(user.id)))
            .sort((a,b) => new Date(b.date).getTime() - new Date(a.date).getTime());
    }, [savedSpeeches, user, eventType]);

    return (
         <Card>
            <CardHeader>
                <CardTitle>Saved Recordings</CardTitle>
                <CardDescription>Review your past performances for this event.</CardDescription>
            </CardHeader>
            <CardContent>
                 {relevantRecordings.length > 0 ? (
                    <ul className="space-y-2">
                        {relevantRecordings.map(rec => (
                            <li 
                                key={rec.id} 
                                className="flex items-center justify-between p-3 rounded-md border bg-muted/50"
                            >
                                <div className="flex-1 min-w-0">
                                    <p className="text-sm font-medium truncate">{rec.topic}</p>
                                    <p className="text-xs text-muted-foreground">Recorded on {format(new Date(rec.date), 'PPP')}</p>
                                </div>
                                <Button size="sm" variant="secondary" disabled>View</Button>
                            </li>
                        ))}
                    </ul>
                ) : (
                    <div className="text-center text-sm text-muted-foreground py-8 border border-dashed rounded-md">
                        No recordings for this event yet.
                    </div>
                )}
            </CardContent>
        </Card>
    )
}

export default function PerformanceEventsPage() {
  const { user, users: allUsers } = useAuth();
  const { toast } = useToast();
  const [activeTab, setActiveTab] = React.useState<PerformanceEventType>('humorous');
  const [isWritingDialogOpen, setIsWritingDialogOpen] = React.useState(false);
  const [speechToEdit, setSpeechToEdit] = React.useState<WrittenSpeech | null>(null);

  const [writtenSpeeches, setWrittenSpeeches] = React.useState<WrittenSpeech[]>(() => {
    if (typeof window === 'undefined') return [];
    try {
      const stored = localStorage.getItem(WRITTEN_SPEECHES_STORAGE_KEY);
      return stored ? JSON.parse(stored) : [];
    } catch (e) {
      return [];
    }
  });

  const [savedSpeeches, setSavedSpeeches] = React.useState<SavedSpeech[]>(() => {
    if (typeof window === 'undefined') return [];
    try {
        const stored = localStorage.getItem(SAVED_SPEECHES_STORAGE_KEY);
        return stored ? JSON.parse(stored) : [];
    } catch (e) {
        return [];
    }
  });
  
  React.useEffect(() => {
    if (typeof window !== 'undefined') {
      try {
        localStorage.setItem(WRITTEN_SPEECHES_STORAGE_KEY, JSON.stringify(writtenSpeeches));
      } catch (e) {
        if (e instanceof DOMException && e.name === 'QuotaExceededError') {
          toast({
            variant: 'destructive',
            title: 'Storage Full',
            description: 'Browser storage is full. Please delete some saved scripts to make space.',
          });
        }
      }
    }
  }, [writtenSpeeches, toast]);

  const handleSaveWrittenSpeech = (title: string, body: string) => {
    if (!user) return;

    if (speechToEdit) {
      // Update
      const updatedSpeech: WrittenSpeech = { ...speechToEdit, title, body, date: new Date().toISOString() };
      setWrittenSpeeches(prev => prev.map(s => s.id === speechToEdit.id ? updatedSpeech : s));
      toast({ title: "Script Updated" });
    } else {
      // Create
      const newSpeech: WrittenSpeech = {
        id: `written-${Date.now()}`,
        ownerId: user.id,
        title,
        body,
        mode: activeTab as PracticeMode,
        date: new Date().toISOString(),
      };
      setWrittenSpeeches(prev => [newSpeech, ...prev]);
      toast({ title: "Script Saved" });
    }
    setIsWritingDialogOpen(false);
    setSpeechToEdit(null);
  };

  const handleDeleteWrittenSpeech = (id: string) => {
    setWrittenSpeeches(prev => prev.filter(s => s.id !== id));
    toast({ title: "Script Deleted", variant: 'destructive' });
  };
  
  const EventContent = ({ eventType }: { eventType: PerformanceEventType }) => (
    <div className="space-y-6 mt-4">
        <SavedScripts 
            eventType={eventType}
            writtenSpeeches={writtenSpeeches}
            setSpeechToEdit={setSpeechToEdit}
            setIsWritingDialogOpen={setIsWritingDialogOpen}
            handleDeleteWrittenSpeech={handleDeleteWrittenSpeech}
            user={user}
        />
        <SavedRecordings 
            eventType={eventType}
            savedSpeeches={savedSpeeches}
            user={user}
            allUsers={allUsers}
        />
    </div>
  );

  return (
    <div className="flex flex-col gap-6">
      <div>
        <h1 className="text-3xl font-bold font-headline">Performance Events</h1>
        <p className="text-muted-foreground">
          Practice for HI, DI, and other interpretation events.
        </p>
      </div>

      <Tabs value={activeTab} onValueChange={(value) => setActiveTab(value as PerformanceEventType)} className="w-full">
        <TabsList className="grid w-full grid-cols-2 md:grid-cols-6">
          <TabsTrigger value="humorous">Humorous</TabsTrigger>
          <TabsTrigger value="dramatic">Dramatic</TabsTrigger>
          <TabsTrigger value="duo">Duo</TabsTrigger>
          <TabsTrigger value="duet">Duet</TabsTrigger>
          <TabsTrigger value="prose">Prose</TabsTrigger>
          <TabsTrigger value="poetry">Poetry</TabsTrigger>
        </TabsList>
        <TabsContent value="humorous"><EventContent eventType="humorous"/></TabsContent>
        <TabsContent value="dramatic"><EventContent eventType="dramatic"/></TabsContent>
        <TabsContent value="duo"><EventContent eventType="duo"/></TabsContent>
        <TabsContent value="duet"><EventContent eventType="duet"/></TabsContent>
        <TabsContent value="prose"><EventContent eventType="prose"/></TabsContent>
        <TabsContent value="poetry"><EventContent eventType="poetry"/></TabsContent>
      </Tabs>

      <WriteSpeechDialog
        isWritingDialogOpen={isWritingDialogOpen}
        setIsWritingDialogOpen={setIsWritingDialogOpen}
        speechToEdit={speechToEdit}
        savedSpeeches={savedSpeeches}
        handleSaveWrittenSpeech={handleSaveWrittenSpeech}
        handleViewArchivedSpeech={() => { /* Not implemented for this page yet */}}
      />
    </div>
  );
}
